.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-cascader-menu-enter-active,
.tu-cascader-menu-leave-active {
  transition: all 0.3s; }

.tu-cascader-menu-enter,
.tu-cascader-menu-leave-active {
  opacity: 0;
  margin-right: -180px; }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #e4ebf5; }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #8394be;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-steps {
  display: flex; }
  .tu-steps--simple {
    padding: 13px 8%;
    border-radius: 4px; }
  .tu-steps--horizontal {
    white-space: nowrap; }
  .tu-steps--vertical {
    height: 100%;
    flex-flow: column; }

.tu-step {
  position: relative;
  flex-shrink: 1; }
  .tu-step:last-of-type .tu-step__line {
    display: none; }
  .tu-step:last-of-type.is-flex {
    flex-basis: auto !important;
    flex-shrink: 0;
    flex-grow: 0; }
  .tu-step:last-of-type .tu-step__main, .tu-step:last-of-type .tu-step__description {
    padding-right: 0; }
  .tu-step__head {
    display: flex;
    position: relative;
    width: 100%; }
    .tu-step__head.is-process {
      color: #8394be;
      border-color: #8394be; }
    .tu-step__head.is-wait {
      color: #c8d0e7;
      border-color: #c8d0e7; }
    .tu-step__head.is-success {
      color: #6bc43f;
      border-color: #6bc43f; }
    .tu-step__head.is-error {
      color: #f56c6c;
      border-color: #f56c6c; }
    .tu-step__head.is-finish {
      color: #6d5dfc;
      border-color: #6d5dfc; }
  .tu-step__icon {
    position: relative;
    z-index: 1;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    font-size: 14px;
    box-sizing: border-box;
    transition: 0.15s ease-out;
    font-weight: 500; }
    .tu-step__icon.is-text {
      border-radius: 50%;
      border: 1px solid;
      border-color: inherit;
      box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff, 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
    .tu-step__icon.is-icon {
      width: 40px; }
  .tu-step__icon-inner {
    display: inline-block;
    user-select: none;
    text-align: center;
    line-height: 1;
    color: inherit; }
    .tu-step__icon-inner[class*="tu-icon"]:not(.is-status) {
      font-size: 20px;
      font-weight: normal; }
    .tu-step__icon-inner.is-status {
      transform: translateY(1px); }
  .tu-step__line {
    width: calc(100% - 20px);
    border-color: inherit;
    background-color: #c8d0e7; }
  .tu-step__line-inner {
    display: block;
    transition: 0.15s ease-out;
    box-sizing: border-box;
    width: 0;
    height: 0px; }
  .tu-step__main {
    white-space: normal;
    text-align: left; }
  .tu-step__title {
    font-size: 14px;
    line-height: 30px; }
    .tu-step__title.is-process {
      color: #8394be; }
    .tu-step__title.is-wait {
      color: #c8d0e7; }
    .tu-step__title.is-success {
      color: #6bc43f; }
    .tu-step__title.is-error {
      color: #f56c6c; }
    .tu-step__title.is-finish {
      color: #6d5dfc; }
  .tu-step__description {
    padding-right: 10%;
    margin-top: -5px;
    font-size: 12px;
    line-height: 20px;
    font-weight: normal; }
    .tu-step__description.is-process {
      color: #8394be; }
    .tu-step__description.is-wait {
      color: #c8d0e7; }
    .tu-step__description.is-success {
      color: #6bc43f; }
    .tu-step__description.is-error {
      color: #f56c6c; }
    .tu-step__description.is-finish {
      color: #6d5dfc; }
  .tu-step.is-horizontal {
    display: inline-block; }
    .tu-step.is-horizontal .tu-step__head {
      align-items: center; }
    .tu-step.is-horizontal .tu-step__line {
      height: 1px;
      top: 11px;
      left: 0;
      right: 0; }
    .tu-step.is-horizontal .tu-step__line-inner {
      border-top: 1px solid;
      border-color: inherit; }
  .tu-step.is-vertical {
    display: flex; }
    .tu-step.is-vertical .tu-step__head {
      flex-grow: 0;
      width: 24px; }
    .tu-step.is-vertical .tu-step__main {
      padding-left: 10px;
      flex-grow: 1; }
    .tu-step.is-vertical .tu-step__title {
      line-height: 24px;
      padding-bottom: 8px; }
    .tu-step.is-vertical .tu-step__line {
      position: absolute;
      width: 1px;
      height: calc(100% - 20px);
      top: 20px;
      left: 10px; }
    .tu-step.is-vertical .tu-step__line-inner {
      border-right: 1px solid;
      border-color: inherit; }
    .tu-step.is-vertical .tu-step__icon.is-icon {
      width: 24px; }
  .tu-step.is-center .tu-step__head {
    left: calc(50% - 10px);
    text-align: center; }
  .tu-step.is-center .tu-step__main {
    text-align: center; }
  .tu-step.is-center .tu-step__description {
    padding-left: 20%;
    padding-right: 20%; }
  .tu-step.is-center .tu-step__line {
    left: 50%;
    right: -50%; }
  .tu-step.is-simple {
    display: flex;
    align-items: center; }
    .tu-step.is-simple .tu-step__head {
      width: auto;
      font-size: 0;
      padding-right: 10px; }
    .tu-step.is-simple .tu-step__icon {
      background: transparent;
      width: 16px;
      height: 16px;
      font-size: 14px; }
    .tu-step.is-simple .tu-step__icon-inner[class*="tu-icon"]:not(.is-status) {
      font-size: 20px; }
    .tu-step.is-simple .tu-step__icon-inner.is-status {
      transform: scale(0.8) translateY(1px); }
    .tu-step.is-simple .tu-step__main {
      position: relative;
      display: flex;
      align-items: stretch;
      flex-grow: 1; }
    .tu-step.is-simple .tu-step__title {
      font-size: 14px;
      line-height: 20px; }
    .tu-step.is-simple:not(:last-of-type) .tu-step__title {
      max-width: 50%;
      word-break: break-all; }
    .tu-step.is-simple .tu-step__arrow {
      flex-grow: 1;
      display: flex;
      align-items: center;
      justify-content: center; }
      .tu-step.is-simple .tu-step__arrow::before, .tu-step.is-simple .tu-step__arrow::after {
        content: "";
        display: inline-block;
        position: absolute;
        height: 7px;
        width: 1px;
        background: #c8d0e7; }
      .tu-step.is-simple .tu-step__arrow::before {
        transform: rotate(-45deg) translateY(-2px);
        transform-origin: 0 0; }
      .tu-step.is-simple .tu-step__arrow::after {
        transform: rotate(45deg) translateY(2px);
        transform-origin: 100% 100%; }
    .tu-step.is-simple:last-of-type .tu-step__arrow {
      display: none; }
